body {
  background-color: black;
}

.hoverc {
  background-color: #fba8a4;
  background-image: linear-gradient(315deg, #fba8a4 0%, #dad2f3 74%);
  padding: 2px;
  border-radius: 10px;
  color: black;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  transition: 0.5s;
}
